<template>
<div>
  <div id="mapid"></div>
</div>
</template>

<script>
export default {
  data() {
    return {
      map: null,
      OSMUrl: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      cofes: [],
    }
  },
  mounted() {
    this.getDataFromApi();
    this.map = this.$utils.map.createMap('mapid');
    this.map.setView([23.8759391,120.588669], 8);
    this.$utils.map.createTileLayer(this.map, this.OSMUrl, { maxZoom: 18 });
    navigator.geolocation.getCurrentPosition((position) => {
      this.map.setView([position.coords.latitude, position.coords.longitude], 16);
    });
  },
  methods: {
    getDataFromApi() {
      this.axios.get('cafes.json')
        .then(res => {
          this.cofes = res.data;
        })
        .finally(() => {
          this.addCluster();
        })
    },
    addCluster() {
      let cluster = this.$utils.map.createMakerCluster();
      for (let i = 0; i < this.cofes.length; i++) {
        let { latitude: lat, longitude: lng } = this.cofes[i];
        let latlng = this.$utils.map.createLatlng([parseFloat(lat), parseFloat(lng)]);
        let marker = this.$utils.map.createMakerByLatlng(latlng);
        marker.bindPopup(this.getPopup(this.cofes[i]));
        cluster.addLayer(marker);
      }
      this.map.addLayer(cluster);
    },
    getPopup(c) {
      return `
        <h3>${c.name}</h3>
        <p>${c.address}</p>
        <p>${c.open_time}</p>
      `
    }
  }
}

</script>

<style>
#mapid {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}
</style>
